@import 'assets/styles/variables';

.registration-partner {
  .page-container {
    padding: 10px;
    margin: 216px 80px 80px 80px ;
    display: flex;
    flex-direction: column;
    align-items: center;

    .registration-banner {
      display: flex;
      flex-direction: column;
      align-items: center;

      &>.title {
        font-family: Gelion;
        font-style: normal;
        font-weight: 500;
        font-size: 91px;
        line-height: 105px;
        color: $dark;
        margin-bottom: 160px;
        max-width: 1165px;
      }

      .options {
        display: flex;
        flex-direction: row;

        .item {
          max-width: 400px;
          
          .type {
            font-weight: 300;
            font-size: 20px;
            line-height: 24px;
            color: $font-content-gray;
          }

          .content {
            font-size: 22px;
            line-height: 26px;
            color: $dark;
          }
        }
      }
    }
  }
}